body, html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fdfdfd;
  text-align: center;
  background-color: #333;
  padding: 20px;
}

h1 {
  margin: 0;
}

.search-form {
  text-align: center;
  margin: 20px auto;
}

.search-form input[type="text"] {
  width: 70%;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.search-form button[type="submit"]:hover {
  background-color: #0056b3;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px;
  display: flex;
}

.photo-card {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

.photo-card img {
  max-width: 100%;
  height: auto;
  border-radius: 5px 5px 0 0;
  display: block;
}

.photo-card .info {
  text-align: center;
  padding: 10px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
  display: block;
}

.load-more:hover {
  background-color: #0356af;
}

